
body {
    user-select: none;
    box-sizing: border-box;
    overflow: hidden;
}


.background {
  width: 1920px;
  height: 1080px;
  background-color: #313131;
}

.title-container {
  position: absolute;
  top: 20px;
  width: 400px;
  height: 100px;
  text-align: center;
  justify-content: center;
  display: flex;
}

.ok-title {
  position: relative;
  height: 60px;
  font-size: 60px;
}

.community-title {
  position: relative;
  font-size: 50px;
  height: 60px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #181818;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
